@use "../abstracts" as *;

.btn {
  &,
  &:link,
  &:visited {
    text-decoration: none;
    font-weight: 600;
    padding: 0.9375em 1.875em;
    border-radius: 0.5em;
    border: none;
    text-transform: uppercase;
    position: relative;
    display: inline-flex;
    align-items: center;
    cursor: pointer;
    transition: all 0.3s;
    will-change: transform;
    box-shadow: 0.125em 0.375em 0.625em rgba(#333, 0.15);
  }

  &:hover {
    transform: translateY(-2px);
    box-shadow: 0.125em 0.75em 0.625em rgba(#333, 0.15);
  }

  &--color {
    background-color: $color-dark;
    color: #fff;
  }

  &:active {
    transform: scale(0.97);
  }

  &:focus {
    outline: transparent;
  }
}
