@keyframes moveRight {
  0% {
    left: 45%;
    opacity: 0;
  }
  50% {
    left: 55%;
    opacity: 1;
  }
  100% {
    left: 50%;
  }
}
@keyframes moveLeft {
  0% {
    right: 55%;
    opacity: 0;
  }
  50% {
    left: 45%;
    opacity: 1;
  }
  100% {
    left: 50%;
  }
}
@keyframes appear {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
*,
*::before,
*::after {
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
  box-sizing: inherit;
  text-decoration: none;
}

@media only screen and (max-width: 78.125em) {
  html {
    font-size: 93.75%;
  }
}
@media only screen and (max-width: 56.25em) {
  html {
    font-size: 87.5%;
  }
}
@media only screen and (max-width: 37.5em) {
  html {
    font-size: 71.25%;
  }
}

body {
  box-sizing: border-box;
  font-family: "Roboto", sans-serif !important;
}

.container {
  max-width: 71.25em;
  margin: 0 auto;
}
@media only screen and (max-width: 78.125em) {
  .container {
    max-width: 58.75em;
  }
}
@media only screen and (max-width: 56.25em) {
  .container {
    max-width: 46.25em;
  }
}
@media only screen and (max-width: 37.5em) {
  .container {
    max-width: 33.75em;
  }
}

.mt-3 {
  margin-top: 1.25em;
}

section {
  margin: 6.25em 0;
}

.u-text-center {
  text-align: center;
}

.center {
  display: flex;
  justify-content: center;
}

.content-wrap {
  padding: 0 1.875em;
}

@font-face {
  font-family: "Roboto";
  src: url(/font/Roboto-Regular.woff) format("woff");
  font-weight: 400;
}
li {
  list-style: none;
}

ul {
  margin: 0 !important;
}

h1 {
  text-align: center;
}

a {
  text-decoration: none !important;
}

.btn, .btn:link, .btn:visited {
  text-decoration: none;
  font-weight: 600;
  padding: 0.9375em 1.875em;
  border-radius: 0.5em;
  border: none;
  text-transform: uppercase;
  position: relative;
  display: inline-flex;
  align-items: center;
  cursor: pointer;
  transition: all 0.3s;
  will-change: transform;
  box-shadow: 0.125em 0.375em 0.625em rgba(51, 51, 51, 0.15);
}
.btn:hover {
  transform: translateY(-2px);
  box-shadow: 0.125em 0.75em 0.625em rgba(51, 51, 51, 0.15);
}
.btn--color {
  background-color: #333;
  color: #fff;
}
.btn:active {
  transform: scale(0.97);
}
.btn:focus {
  outline: transparent;
}

.nav-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1.875em;
}

.logo {
  height: 6.25em;
}

.site-header {
  padding-top: 0 !important;
  margin: 0 !important;
  border-bottom-color: transparent !important;
}

.dropdown {
  position: relative;
  display: inline-block;
  transition: all 3s;
}
.dropdown__link {
  color: #333;
  transition: 0.3s;
}
.dropdown__link:hover {
  color: #0077fe;
}
.dropdown:hover .dropdown__content {
  display: block;
}
.dropdown__content {
  display: none;
  position: absolute;
  background-color: #f1f1f1;
  min-width: 10em;
  box-shadow: 0px 8px 1em 0px rgba(0, 0, 0, 0.2);
  padding: 0.75em 1em;
  z-index: 1;
  transition: all 3s;
  border-radius: 5px;
}

.menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.menu-item a {
  font-size: 0.875em;
  font-weight: 600;
  text-transform: uppercase;
  transition: 0.3s;
  font-weight: 500;
  color: #333;
}
.menu-item a:hover {
  color: #0077fe;
}

.menu-header-menu li a {
  margin: 6.25em 0;
}

.menu-header-menu-container .menu .menu-item:not(:last-child) {
  padding-right: 1.0625em;
}

.hidden-cross {
  display: none;
  font-size: 3rem;
  color: #333;
  cursor: pointer;
}
@media only screen and (max-width: 56.25em) {
  .hidden-cross {
    display: block;
  }
}

.sidebar {
  backdrop-filter: blur(1rem);
  position: fixed;
  display: none;
  top: 0;
  right: 0;
  height: 100vh;
  z-index: 11;
  width: 50%;
  background-color: rgba(255, 255, 255, 0.8);
  box-shadow: -1rem 0 1rem rgba(0, 0, 0, 0.2);
}
@media only screen and (max-width: 37.5em) {
  .sidebar {
    width: 100%;
  }
}

.sidebar li,
.sidebar li a {
  width: 100%;
  padding-bottom: 1.5rem;
}

.sidebar-menu-list {
  padding: 2rem 6rem;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.sidebar-menu-list li a {
  color: #333;
}

.icon-x {
  transition: 0.2s;
  padding: 2.5rem;
  font-size: 4rem;
}

.footer-box {
  display: flex;
  justify-content: center;
}

.section-frontpage {
  background-image: url(../src/images/blue.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 85vh;
  position: relative;
  border-radius: 40px;
  margin: 0 30px;
}

.text-box {
  text-align: center;
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  width: 100%;
}

